.product-image {
  padding: 10px 0px 10px 10px;
}

.product-image img {
  border: 5px solid white;
  border-radius: 10px;
  width: 300px;
  height: 200px;
}

.product-price {
  margin-top: 5px;
  margin-left: 20px;
  color: rgb(13, 13, 13);
  font-weight: bold;
}

.product-title {
  margin-top: 200px;
  margin-left: 200px;
  color: rgb(12, 12, 12);
  font-weight: bold;
}

