@media screen and (max-width: 320px) {
  .product-container {
    position: relative;

padding:5px ;  
display: grid;
column-gap: -100px; 

   
  }


  .product-image img {
    width: 100px; 
    height: 50px; 
  }

  .product-title {
    margin-top: 5px;
    margin-left: 5px;
  }
}


 







